.header{
  /* background-color: rgb(5, 51, 143); */
  background-image: linear-gradient(120deg,rgb(248, 44, 180),rgb(5, 51, 143));
  height: 75px;
  width: 100%;

}
.headerText{
  text-transform: capitalize;
  color: white;
  font-weight: bold;
  text-align: center;
  font-size: 40px;
  padding-top: 10px;
  font-family: Century Gothic;
}
.textFont{
  font-family: Bahnschrift;
}
.slideOneText{
  text-transform: capitalize;
  font-size: 20px;
  font-weight: normal;
  padding-left: 5px;
  color: rgb(0, 56, 165);
  border-bottom: 2px solid skyblue;
  padding-bottom: 8px;
}
.slideOneHeadText{
  color: rgb(24, 65, 145);
  border-bottom: 2px solid skyblue;
  padding-bottom: 6px;
  font-family: Century Gothic;
}
.chatBotText{
  text-transform: capitalize;
  font-weight: bold;
  font-size: 30px;
}
.chatBotSubTextDiv{
  text-transform: capitalize;
}
.chatBotSubText{
  text-transform: capitalize;
  font-size: 25px;
  font-weight: bold;
  color: rgb(24, 65, 145);
}
.chatBot{
  height: 300px;
  background-image: linear-gradient(120deg,#bdc3c7,rgb(109,132,163));
}
.clickOne{
  background-color: rgb(0, 177, 63);
  border: 2px solid rgb(0, 177, 63);
  text-transform: capitalize;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}
.clickTwo{
  background-color: rgb(0, 177, 63);
  border: 2px solid rgb(0, 177, 63);
  text-transform: capitalize;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}
.clickThree{
  background-color: rgb(0, 177, 63);
  border: 2px solid rgb(0, 177, 63);
  text-transform: capitalize;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}
.slidingImg{
  width: 100%;
  height: 100%;
}
.sentimentAnalysis{
  height: 300px;
  background-image: linear-gradient(120deg,#bdc3c7,rgb(109,132,163));
}
.saText{
  text-transform: capitalize;
}
.aiText{ 
  text-transform: uppercase;
}
.aiSubText{
  text-transform: uppercase;
}
